
#app2{
    outline:1px solid blue;
}

#app2 .tab-bar{
    display:flex;
}

#app2 .tab-bar > li{
    border:1px solid black;
    width:50%;
}

#app2 .tab-content > li{
    display:none;
}

#app2 .tab-bar > li.selected{
    background: red;
    color:white;
}

#app2 .tab-content > li.active{
    display:block;
}

